import { Modal, ModalBody, ModalContent, ModalHeader, Spacer, useDisclosure } from "@nextui-org/react";
import './gallery.css'

function shuffle(array: any[]) {
    let currentIndex = array.length;
  
    // While there remain elements to shuffle...
    while (currentIndex != 0) {
  
      // Pick a remaining element...
      const randomIndex = Math.floor(Math.random() * currentIndex);
      currentIndex--;
  
      // And swap it with the current element.
      [array[currentIndex], array[randomIndex]] = [
        array[randomIndex], array[currentIndex]];
    }

    return array;
}

const images = shuffle([
    '/siding2.jpg', '/siding5.jpg', '/siding6.jpg', '/siding7.jpg', '/siding8.jpg', '/siding9.jpg', '/siding10.jpg',
    '/gutters1.jpg',
    '/patio.jpg', '/patio2.jpg', '/patio3.jpg',

]);

const bigImages = images.splice(0, 4);
const smallImages = images.splice(0, 6);



export default function Gallery() {
    return (
        <div>
            <h2 className="text-center leading-10 tracking-tight font-semibold text-xl">Gallery</h2>
            <Spacer y={1} />

            <div style={{display: 'flex', flexWrap: 'wrap', justifyContent: 'center', gap: '10px'}}>
                {bigImages.map((image, index) => (
                    <ImageModal image={image} imageClassName="big-image" key={index} />
                ))}
            </div>

            <Spacer y={3} />

            <div style={{display: 'flex', flexWrap: 'wrap', justifyContent: 'center', gap: '10px'}}>
                {smallImages.map((image, index) => (
                    <ImageModal image={image} imageClassName="small-image" key={index} />
                ))}
            </div>
        </div>
    )
}

function ImageModal(props: {image: string, imageClassName: string}) {
    const { isOpen, onOpen, onClose } = useDisclosure();

    return (
        <div style={{alignSelf: 'center'}}>
            <img src={props.image} className={props.imageClassName} onClick={onOpen}/>

            <Modal isOpen={isOpen} backdrop="blur" placement="center" onClose={onClose}>
                <ModalContent>
                    <ModalHeader>Gallery</ModalHeader>
                    <ModalBody>
                        <img src={props.image} />
                    </ModalBody>
                </ModalContent>
               
            </Modal>
        </div>
    )
}